<template>
	<div class="container home" :style="{backgroundImage: 'url(' + bgImg + ')'}"  v-wechat-title="$route.meta.title">

		<!---->
		<div class="prizeView div_float">
			<div class="L forbid" @click="showPrizeFun"></div>
			<div class="R forbid" @click="showRulesFun"></div>
		</div>

		<!---->
		<div class="palyMusic div_flaot">  <!--audioPlay bgPlay-->
			<!---->
			<!--<div class="" v-if='audioPlay' data-id='1'  :class="[audioPlay ? 'palyIcon playDh':'noPaly']" @click="playMusic"></div>-->
			<!---->
			<div class=""   data-id='2'  :class="[bgPlay ? 'palyIcon playDh':'noPaly']" @click="playMusicGlob"></div>
			
			<!--<audio controls="controls"  loop="loop"  ref="audio"  id="audio" style="display: none;">
			  <source :src="audioSrc"  type="audio/mp3" />
			</audio>-->
		</div>

		<!---->
		<div class="mePlay forbid" :class="[isBig ? 'mePlayLog':'']" @click="playFun"></div>

		<!--2-->
		<!--<div class="hBg2"></div>
		<div class="hBg3"></div>
		<div class="hBg4"></div>-->

		<!---->
		<div class="prizeLayer" v-show="showPrLayer">
			<div class="sub">
				<div class="close" @click="showPrizeFun"></div>
				<!---->
				<div class="cnView">
					<div class="til"></div>
					<div class="subTil">{{prizeList.score_sum}}</div>

					<!---->
					<div class="zjView" v-if="prizeList.award != ''">
						<div class="swiper-container">
							<div class="swiper-wrapper">
								<div class="swiper-slide" v-for="(item,index) in prizeList.award" :key="item.id">
									<div class="imgView">
										<img :src="item.award_img" />
									</div>
									<div class="til">{{item.award_name}}</div>
									<!---->
									<div class="isLQ" v-if="item.status == 1"></div>
									<!---->
									<div class="WLQ" :data-id='item.id' :data-s-type='item.award_type' v-else @click="inputMsgFun"></div>
								</div>
							</div>
							<!--  -->
							<div class="swiper-button-prev leftIcon"></div>
							<div class="swiper-button-next rightIcon"></div>
						</div>
					</div>

					<!---->
					<div class="wzjView" v-else>
						<div></div>
						<div>~</div>
					</div>
				</div>

			</div>
		</div>

		<!---->
		<info-layer :province='province' :city='city' :store='store' :showInLayer='showInLayer' @showInfo='showInfoFun' :provinceText="provinceText" :cityText="cityText" :storeText="storeText" @selProvince='selProvinceFun' @selCity='selCityFun' @selStore='selStoreFun' @submitInfo='submitInfoFun' />

		<!---->
		<div class="rulesLayer" v-show="showRules">
			<div class="sub">
				<div class="close" @click="showRulesFun"></div>
				<div class="cnView">
					<div class="subView">
						<div class="til"></div> <!-- {{rulesText}}-->
						<div class="rCn"  v-html='rulesText'></div>
					</div>
					<img class="code" src="../../assets/code.png" />
				</div>
			</div>
		</div>

		<!---->
		<div class="noPlay" v-show="showNoPlay">
			<div class="sub">
				<div class="close" @click="showNoPlayFun"></div>
				<div class="cnView">
					<div class="til">Sorry</div>
					<div class="nCn">
						<div></div>
						<div></div>
					</div>
				</div>
			</div>
		</div>

		<!---->
		<div class="layerView" v-show="noRegister">
			<div class="sub">
				<div class="CnView">
					<div class="til"></div>
					<div class="tsCn wTsCn">
						<div></div>
						<div></div>
					</div>
					<div class="okBtn wTsBtn" @click="registerFun"></div>
				</div>
			</div>
		</div>
		
		
		<!---->
		<div class="layerView" v-show="phoneView">
			<div class="sub">
				<div class="close phoneClose" @click="closePhoneFun"></div>
				<div class="CnView">
					<div class="til">!</div>
					<div class="phoneView">
						<div></div>
						<input v-model="flowPhone" type="text" placeholder="" />
					</div>
					<div class="okBtn" @click="getFlowFun"></div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import wx from 'weixin-js-sdk'
	//swiper
	import Swiper from 'swiper'
	//
	import infoLayer from '../../components/infoLayer'

	export default {
		data() {
			return {
				Request: this.$api, //
				util: this.$util.util.prototype, //
				prizeList: '', //
				showPrLayer: false, //
				showInLayer: false, //
				province: {
					anchor: [],
					textTitle: '',
					data: [
						[]
					]
				},
				city: {
					anchor: [],
					textTitle: '',
					data: [
						[]
					]
				},
				store: {
					anchor: [],
					textTitle: '',
					data: [
						[]
					]
				},
				provinceText: '',
				cityText: '',
				storeText: '',
				showRules: false, //
				showNoPlay: false, //
				rulesText: '', //		
				isBig: false, //
				noRegister: false, //
				registerUrl: '', //
				shareInfo: '', //
				hasPlay: '', //
				bgImg: '', //   
				gameTil: '', //
				storeMsg: '', //
				selStoreIndex: null, //
				postId: '', //id
				shareMessage: '', //				
				audioSrc:'https://chj-oss.oss-cn-hangzhou.aliyuncs.com/gashapon/mp3/music.mp3',//
				audioPlay:false,//
				phoneView: false, //
				flowPhone: '', //
			}
		},
		mounted() {
			//swiper
			new Swiper('.swiper-container', {
				//				loop: true,
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
				},
				observer: true, //
				observeParents: true //
			})

		},
		components: {
			'info-layer': infoLayer,
		},
		computed: {
			bgm() {
				return this.$store.getters.bgm;
			},
			bgPlay() {
				return this.$store.getters.bgPlay;
			},
		},
		methods: {
			//
			async setConfig() {
				var that = this;
				var href = location.href.split('#')[0];
				await that.Request.setWxConfig(href)
					.then(res => { 
						if(res.code == 0) {
							wx.config({
								debug: false, // ,apialertpclogpc
								appId: res.data.appId, // 
								timestamp: res.data.timestamp, // 
								nonceStr: res.data.nonceStr, // 
								signature: res.data.signature, // 
								jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // JS
							});
						}
						
						
						//
						if(that.bgm == null) {
							var audioView = new Audio();
							audioView.src = that.audioSrc;
							audioView.loop = 'loop';
							//vuexbgm
							that.$store.commit('changeBgm', audioView);
						}
						
					})

				//
				var share = that.shareMessage;

				wx.ready(function() {
					//
					wx.onMenuShareAppMessage({
						title: share.title, // 
						desc: share.desc, // 
						link: window.location.href.split('#')[0], // JS
						imgUrl: share.img, // 
						success: function() {
							//
							that.postOperFun('share');
						}
					});

					//
					wx.onMenuShareTimeline({
						title: share.title, // 
						desc: share.desc, // 
						link: window.location.href.split('#')[0], // JS
						imgUrl: share.img, // 
						success: function() {
							//
							that.postOperFun('share');
						}
					});
					
					
					if(that.bgPlay){
						//
						that.bgm.play();
						// 
						that.$store.commit('changePlay', true);
					}
					
					
				});
			}, 
			///  
			showPrizeFun() {
				var sign = true;
				if(this.showPrLayer) {
					sign = false;
				}
				this.showPrLayer = sign;
			},
			///  
			showInfoFun(e) {
				var sign = true;
				if(this.showInLayer) {
					sign = false;
				}
				this.showInLayer = sign;
				

				//
				if(sign) {
					//id
					this.postId = e.currentTarget.dataset.id;
					//
					this.showPrLayer = false;
				}

			},
			//
			closePhoneFun(){
				this.phoneView = false;
			},
			///  
			showRulesFun() {
				var sign = true;
				if(this.showRules) {
					sign = false;
				}
				this.showRules = sign;
			},
			///  
			showNoPlayFun() {
				var sign = true;
				if(this.showNoPlay) {
					sign = false;
				}
				this.showNoPlay = sign;
			},
			// 
			playFun() {
				var that = this;
				//
				if(!this.hasPlay) {
					this.showNoPlay = true;
					return;
				}
				
//				//
//				document.getElementById('audio').pause();
//				
//				//vuexbgm
//				this.$store.commit('changeBgm', null);
//				
//				//
//				var audioView = new Audio();
//				audioView.src = process.env.BASE_URL+"mp3/music.mp3";
//				audioView.loop = 'loop';
//				//vuexbgm
//				this.$store.commit('changeBgm', audioView);
//				//
//				this.bgm.play();
//				// 
//				this.$store.commit('changePlay', false);
//				

				this.$router.replace({
					path: '/play'
				})
			},
			// ()
			playMusic() {
				var that = this;
				//
				var bgm = document.getElementById('audio');
				//
				if(bgm != null) {
					if(bgm.paused) {
						bgm.play(); //   
						// 
						that.audioPlay = true;
					} else {
						bgm.pause(); // 
						// 
						that.audioPlay = false;
					}
				}

			},
			// ()
			playMusicGlob() {
				var that = this;
				//
				var bgm = that.bgm;
				//
				if(bgm != null) {
					if(bgm.paused) {
						bgm.play(); //   
						// 
						that.$store.commit('changePlay', true);
					} else {
						bgm.pause(); // 
						// 
						that.$store.commit('changePlay', false);
					}
				}

			},
			//
			async getRulesFun() {
				var that = this;
				await that.Request.getGameInfo()
					.then(res => {
						if(res.code == 0) {
							//
							that.rulesText = res.data.rule;
							//
							that.bgImg = res.data.img;
							//
							that.gameTil = res.data.name;

							that.$route.meta.title = res.data.name;
						}
					})
			},
			//
			getProvinceFun() {
				var that = this;
				that.Request.getProvince()
					.then(res => {
						if(res.code == 0) {
							var signArr = [];
							for(var x in res.data) {
								signArr.push(res.data[x].province);
							}
							that.province.data = [signArr];
						}
					})
			},
			//
			selProvinceFun(e) {
				this.provinceText = e[0].value;
				//  
				this.cityText = '';
				this.storeText = '';
				//
				this.$indicator.open({
					spinnerType: 'fading-circle'
				});
				//
				this.getCityFun().then(res => {
					this.$indicator.close();
				});
			},
			//
			async getCityFun() {
				var that = this;
				await that.Request.getCity(this.provinceText)
					.then(res => {
						if(res.code == 0) {
							var signArr = [];
							for(var x in res.data) {
								signArr.push(res.data[x].city);
							}
							that.city.data = [signArr];
						}
					})
			},
			//
			selCityFun(e) {
				this.cityText = e[0].value;
				//
				this.storeText = '';
				//
				this.$indicator.open({
					spinnerType: 'fading-circle'
				});
				//
				this.getStoreFun().then(res => {
					this.$indicator.close();
				});
			},
			//
			async getStoreFun() {
				var that = this;
				await that.Request.getStore(this.cityText)
					.then(res => {
						if(res.code == 0) {
							var signArr = [];
							for(var x in res.data) {
								signArr.push(res.data[x].title);

								//
								that.storeMsg = res.data;
							}
							that.store.data = [signArr];
						}
					})
			},
			//
			selStoreFun(e) {
				this.storeText = e[0].value;
				this.selStoreIndex = e[0].index;
			},
			//
			submitInfoFun(e) {
				var that = this;
				var userName = e.name;
				var userPhone = e.phone;
				

				if(userName == '') {
					that.$toast('');
					return;
				}

				if(userPhone == '') {
					that.$toast('');
					return;
				} else {
					if(!that.util.isTel(userPhone) && !that.util.isMobile(userPhone)) {
						that.$toast('');
						return;
					}
				}
				
				if(this.selStoreIndex == null){
					that.$toast('');
					return;
				}
				
				//id
				var storeId = this.storeMsg[this.selStoreIndex].id;

		
				//
				that.$indicator.open({
					spinnerType: 'fading-circle'
				});

				that.Request.postPrizeMsg(this.$cookies.get('openId'), that.postId, userName, userPhone, storeId)
					.then(res => {
						if(res.code == 0) {

							//
							that.postOperFun('cash');

							//
							//
							that.myPrizeFun()
								.then(res => {
									//
									that.$indicator.close();

									//
									that.showInLayer = false;

									//
									that.$toast('');
									//
									that.city.data = [
										[]
									];
									that.store.data = [
										[]
									];
									that.provinceText = '';
									that.cityText = '';
									that.storeText = '';
								});
						}
					});

			},
			//
			postOperFun(action) {
				var that = this;
				that.Request.postOper('index', this.$cookies.get('openId'), action, navigator.userAgent, that.util.getOS())
					.then(res => {
						//console.log(res)
					})
			},
			//UV
			getUV() {
				var that = this;
				//
				var timestamp = Date.parse(new Date());
				//
				var nowDayTime = this.util.pastTime();
				if(timestamp > localStorage.getItem('pvTime') || localStorage.getItem('pvTime') == null) {
					that.Request.getUV(this.$cookies.get('openId'))
						.then(res => {
							if(res.code == 0) {
								localStorage.setItem('pvTime', nowDayTime);
							}
						})
				}
			},
			//
			registerFun() {
				window.location.href = this.registerUrl;
			},
			//
			async isRegisterFun() {
				var that = this;
				await that.Request.isRegister(this.$cookies.get('openId'))
					.then(res => {
						if(res.code == 0) {
							if(res.data.regist_status == 1) {
//								that.noRegister = true;
								that.registerUrl = res.data.url;
							}
						}
					})
			},
			//
			async getShareFun() {
				var that = this;
				await that.Request.getShare()
					.then(res => {
						if(res.code == 0) {
							var sign = {
								title: res.data.title,
								descr: res.data.descr,
								img: res.data.img
							};
							that.shareInfo = sign;
						}
					})
			},
			//
			palyChanceFun() {
				var that = this;
				that.Request.palyChance(this.$cookies.get('openId'))
					.then(res => {
						if(res.code == 0) {
							if(res.data.is_play == 1) {
								that.hasPlay = true;
							} else {
								that.hasPlay = false;
							}
						}
					})
			},
			//
			async myPrizeFun() {
				var that = this;
				await that.Request.myPrize(this.$cookies.get('openId'))
					.then(res => {
						if(res.code == 0) {
							that.prizeList = that.util.conJson(res.data);
						}
					})
			},

			//
			async shareMsgFun() {
				var that = this;
				await that.Request.shareMsg()
					.then(res => {
						console.log(res)
						if(res.code == 0) {
							that.shareMessage = that.util.conJson(res.data);
						}
					})
			},
			//
			getFlowFun() {
				var that = this;
				var phone = that.flowPhone;
				console.log(phone)

				if(phone == '') {
					this.$toast('');
					return;
				}

				if(!that.util.isMobile(phone)) {
					this.$toast('');
					return;
				}

				//
				that.$indicator.open({
					spinnerType: 'fading-circle'
				});

				that.Request.postPrizeMsg(this.$cookies.get('openId'), that.postId, '', phone, '')
					.then(res => {
						//						console.log(res)
						if(res.code == 0) {

							//
							that.postOperFun('cash');

							//
							that.$indicator.close();

							//
							that.phoneView = false;

							//
							that.$toast('');

						}
					})
			},
			
			//
			inputMsgFun(e){
				var  sType = e.currentTarget.dataset.sType;
				console.log(sType)
				//0:1
				if(sType == 0){
					//
					this.showInfoFun(e);
				}else{
					//
					this.phoneView = true;
					//id
					this.postId = e.currentTarget.dataset.id;
					//
					this.showPrLayer = false;
				}
			},
		},
		created() {
			var that = this;
			//
			if(that.bgm == '') {
				var audioView = new Audio();
				that.$store.commit('changeBgm2', audioView);
			}

			//672 
			if(document.documentElement.clientHeight > 672) {
				this.isBig = true;
			}


			//
			that.$indicator.open({
				spinnerType: 'fading-circle'
			});

			//
			that.isRegisterFun();
			//
			that.getShareFun();
			//
			that.getRulesFun().then(res =>{
				that.$route.meta.title = that.gameTil; 
			});
			//
			that.getProvinceFun();
			//
			that.postOperFun('browse');
			//
			that.palyChanceFun();
			//
			that.myPrizeFun();
			//UV
			that.getUV();
			//
			that.shareMsgFun()
				.then(res => {
					//  
					that.setConfig().then(res =>{
						that.$indicator.close();
					});
				});

		},
		destroyed(){
			//
//			document.getElementById('audio').pause();
		}

	}
</script>

<style scoped lang="less">
	/**/
	
	.layerView {
		position: fixed;
		width: 100%;
		top: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, .4);
		z-index: 3;
		.sub {
			margin: 0 auto;
			margin-top: 2.28rem;
			width: 6.11rem;
			height: 6.23rem;
			position: relative;
			background: url(../../assets/layerBg.png) no-repeat;
			background-size: 100% 100%;
			/**/
			.phoneView {
				margin-top: .75rem;
				>div {
					color: #737477;
					font-size: .3rem;
					text-align: center;
				}
				input {
					width: 4.12rem;
					height: .86rem;
					border-radius: .08rem;
					border: solid .02rem #d0d0d0;
					padding-left: .15rem;
					color: #a0a0a0;
					font-size: .3rem;
					margin-top: .38rem;
					outline: none;
				}
			}
			/**/
			.CnView {
				padding: 0 .9rem;
				padding-top: .001rem;
				.til {
					color: #fc699e;
					font-size: .6rem;
					margin-top: .5rem;
					text-align: center;
				}
				.tsCn {
					margin-top: .3rem;
					color: #737477;
					font-size: .3rem;
					line-height: .45rem;
				}
				.wTsCn {
					text-align: center;
					margin-top: .92rem;
				}
				.okBtn {
					width: 2.69rem;
					height: 1.06rem;
					background: url(../../assets/btnBg.png) no-repeat;
					background-size: 100% 100%;
					line-height: .8rem;
					font-size: .34rem;
					color: white;
					text-align: center;
					margin: 0 auto;
					margin-top: .4rem;
				}
				.wTsBtn {
					margin-top: 1rem;
				}
				.zjTBtn {
					margin-top: 1.4rem;
				}
			}
			.phoneClose{
				bottom: -1.25rem !important;
			}
		}
	}
	
	.noPlay {
		position: fixed;
		z-index: 3;
		top: 0;
		bottom: 0;
		width: 100%;
		background-color: rgba(0, 0, 0, .4);
		.sub {
			width: 6.11rem;
			height: 6rem;
			border-radius: .08rem;
			margin: 0 auto;
			margin-top: 3.2rem;
			position: relative;
			text-align: center;
			.cnView {
				width: 6.11rem;
				height: 5.49rem;
				background: url(../../assets/t_one.png) no-repeat;
				background-size: 100% 100%;
				padding-top: .001rem;
				>.til {
					text-align: center;
					font-size: .6rem;
					color: #fc699e;
					margin-top: 1.01rem;
				}
				.nCn {
					margin-top: .79rem;
					font-size: .3rem;
					color: #737477;
				}
			}
		}
	}
	/*   */
	
	.prizeLayer .zjView {
		width: 5rem;
		height: 5.4rem;
		padding: 0 2%;
		margin: 0 auto;
		margin-top: .2rem;
		position: relative;
		.swiper-container {
			font-size: 0;
			.swiper-button-next,
			.swiper-button-prev {
				outline: none;
			}
		}
		.swiper-wrapper {
			text-align: center;
			font-size: 0;
			width: 75%;
			.swiper-slide {
				height: 5.4rem;
				>.til,
				.subTil {
					color: #737477;
					font-size: .3rem;
					margin-top: .35rem;
				}
				>.subTil {
					margin-top: .89rem;
				}
				.isLQ {
					color: #737477;
					font-size: .3rem;
					margin-top: .6rem;
				}
				.WLQ {
					width: 2.69rem;
					height: 1.06rem;
					background: url(../../assets/btnBg.png) no-repeat;
					background-size: 100% 100%;
					color: white;
					text-align: center;
					line-height: .8rem;
					font-size: .3rem;
					margin: 0 auto;
					margin-top: .6rem;
				}
			}
			.imgView {
				width: 2.5rem;
				height: 2.5rem;
				line-height: 2.5rem;
				font-size: 0;
				margin: 0 auto;
				margin-top: .2rem;
				background: url(../../assets/zjBg.png) no-repeat;
				background-size: 100% 100%;
				text-align: center;
				img {
					width: 1.5rem;
					height: 1.5rem;
					border-radius: 100%;
					display: inline-block;
					vertical-align: middle;
					background-color: white;
				}
			}
		}
	}
	/*   */
	
	.prizeLayer .wzjView {
		margin-top: 1rem;
		text-align: center;
		color: #737477;
		font-size: .3rem;
		line-height: .45rem;
	}
	/**/
	
	.prizeLayer,
	.rulesLayer {
		position: fixed;
		z-index: 3;
		top: 0;
		bottom: 0;
		width: 100%;
		background-color: rgba(0, 0, 0, .4);
		.sub {
			width: 6.61rem;
			height: 9.15rem;
			/*background-color: white;*/
			border-radius: .08rem;
			margin: 0 auto;
			margin-top: 1rem;
			position: relative;
			.cnView {
				height: 8.3rem;
				background: url(../../assets/prizeBg.png) no-repeat;
				background-size: 100% 100%;
				padding: 0 .45rem;
				padding-top: .001rem;
				>.til {
					text-align: center;
					margin-top: .68rem;
					font-size: .6rem;
					color: #fc699e;
				}
				>.subTil {
					text-align: center;
					font-size: .3rem;
					color: #737477;
					margin-top: .24rem;
				}
			}
		}
	}
	/**/
	
	.rulesLayer {
		.sub {
			width: 95%;
			height: 10rem;
			.cnView {
				height: 8.94rem;
				background: url(../../assets/rulesBg.png) no-repeat;
				background-size: 100% 100%;
				padding: 0 .52rem;
				position: relative;
				.subView {
					padding: 0 5%;
					padding-top: .001rem;
					.til {
						text-align: left;
						color: #fc699e;
						font-size: .36rem;
						margin-top: .58rem;
					}
					.rCn {
						margin-top: .2rem;
						color: #737477;
						font-size: .28rem;
						height: 5.8rem;
						overflow-y: auto;
					}
				}
				.code {
					position: absolute;
					width: 1.38rem;
					height: 1.4rem;
					margin: auto;
					left: 0;
					right: 0;
					bottom: .35rem;
				}
			}
		}
	}
	
	.mePlay {
		width: 3.25rem;
		height: 2.51rem;
		position: absolute;
		margin: 0 auto;
		left: 0;
		right: 0;
		z-index: 3;
		bottom: 1.3rem;
		background: url(../../assets/palyBtn.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.mePlayLog {
		bottom: 2.3rem;
	}
	
	.palyMusic {
		padding: 0 5%;
		margin-top: .29rem;
	}
	
	.palyMusic>div {
		float: right;
		display: inline-block;
		vertical-align: middle;
		width: .48rem;
		height: .48rem;
		border-radius: 100%;
		color: white;
		font-size: .24rem;
		text-align: center;
		line-height: .45rem;
		position: relative;
	}
	
	.playDh {
		animation: playAm 5s linear infinite;
	}
	/**/
	
	.palyIcon {
		background: url(../../assets/y-play.png) no-repeat;
		background-size: 100% 100%;
	}
	/**/
	
	.noPaly {
		background: url(../../assets/n-play.png) no-repeat;
		background-size: 100% 100%;
	}
	
	@keyframes playAm {
		to {
			transform: rotate(0deg);
		}
		from {
			transform: rotate(-360deg);
		}
	}
	
	.prizeView {
		padding: 0 5%;
		margin-top: .68rem;
		height: .6rem;
		line-height: .6rem;
		font-size: .28rem;
		color: white;
		.L {
			float: left;
		}
		.R {
			float: right;
		}
	}
	
	.home {
		height: 100%;
		width: 100%;
		/*background: url(../../assets/hBg2.png) no-repeat;*/
		background-repeat: no-repeat;
		/*background-size: 100% 100%;*/
		padding-top: .001rem;
		position: relative;
		background-size: 100% auto;
	}
	
	.hBg2 {
		position: absolute;
		margin: 0 auto;
		top: 2.1rem;
		bottom: 0;
		width: 100%;
		height: 6.68rem;
		background: url(../../assets/hbg5.png) no-repeat;
		background-size: 100% 100%;
		z-index: 3;
	}
	
	.hBg3 {
		position: absolute;
		margin: 0 auto;
		top: 6.1rem;
		right: 0;
		width: 2.85rem;
		height: 4.2rem;
		background: url(../../assets/hbg4.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.hBg4 {
		position: absolute;
		margin: 0 auto;
		bottom: 0;
		left: 0;
		width: 4.06rem;
		height: 5rem;
		background: url(../../assets/hbg3.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.leftIcon {
		width: .7rem;
		height: .7rem;
		background: url(../../assets/left.png) no-repeat !important;
		background-size: 100% 100% !important;
		margin-top: -1.5rem;
	}
	
	.rightIcon {
		width: .7rem;
		height: .7rem;
		background: url(../../assets/right.png) no-repeat !important;
		background-size: 100% 100% !important;
		margin-top: -1.5rem;
	}
	
	.close {
		position: absolute;
		width: .91rem;
		height: .89rem;
		background: url(../../assets/close.png) no-repeat;
		background-size: 100% 100%;
		margin: auto;
		left: 0;
		right: 0;
		bottom: -.25rem;
	}
</style>


